import React, { useEffect, useState } from 'react';
import { Layout } from 'antd';
import { HashRouter as Router, Routes, Route, Navigate } from 'react-router-dom';
import Home from './pages/index';
import Profile from './pages/Profile';
import Login from './pages/Login';
import Register from './pages/Register';
import MenuSidebar from './components/Menu';

const { Sider, Content } = Layout;
// window.RV_CONFIGS ={
//   site_url: window.location.host,
//     	ajax_url:"https://lotilife.com/wp-admin/admin-ajax.php",
//         user_id:"",
//         user_login:"",
//         is_login:false,
// }
function App() {
    const [isLoggedIn, setIsLoggedIn] = useState(() => {
        // Try to get login status from localStorage, fallback to window.RV_CONFIGS
        return localStorage.getItem('is_login') === 'true' || window.RV_CONFIGS.is_login;
    });

    useEffect(() => {
        const currentHash = window.location.hash;
        if (!isLoggedIn && currentHash !== '#/login' && !currentHash.startsWith('#/register')) {
            window.location.href = '#/login';
        }
    }, [isLoggedIn]);

    useEffect(() => {
        // Sync localStorage with state
        localStorage.setItem('is_login', isLoggedIn);
    }, [isLoggedIn]);

    return (
        <Router>
            <Layout style={{ minHeight: '50vh' }}>
                {isLoggedIn && (
                    <Sider style={{ background: 'none' }} collapsible>
                        <MenuSidebar />
                    </Sider>
                )}
                <Layout>
                    <Content style={{ margin: '16px' }}>
                        <Routes>
                            {isLoggedIn ? (
                                <>
                                    <Route path="/" element={<Home />} />
                                    <Route path="/profile" element={<Profile />} />
                                    <Route path="*" element={<Navigate to="/" />} />
                                </>
                            ) : (
                                <>
                                    <Route path="/login" element={<Login />} />
                                    <Route path="/register" element={<Register />} />
                                    <Route path="*" element={<Navigate to="/login" />} />
                                </>
                            )}
                        </Routes>
                    </Content>
                </Layout>
            </Layout>
        </Router>
    );
}

export default App;
